.img-section {
	margin: 0 0 20px;
	.img-holder {
		position: relative;
	}
	.main-img {
		display: block;
		width: 100%;
		height: auto;
	}
	.logo-shop {
		position: absolute;
		left: 50%;
		top: 70%;
		transform: translate(-50%, -70%);
		-webkit-transform: translate(-50%, -70%);
		-ms-transform: translate(-50%, -70%);
		a {
			display: block;
			height: 100%;
		}
	}
	.shop {
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translateX(-50%);
		padding: 8px 60px;
		background: rgba(255, 255, 255, .6);
		border-radius: 5px 5px 0 0;
		font-size: $font-size+4;
	}
}